import {
  ASYNC_START,
  ASYNC_END,
  GET_HEADER_INFO,
} from '../../constants/actionTypes';

export default (state = {}, action) => {
  switch (action.type) {
    case ASYNC_START:
      return { ...state };
    case ASYNC_END:
      return { ...state };
    case GET_HEADER_INFO:
      return { ...state, headerInfo: action.payload.feature };
    default:
      return state;
  }

  return state;
};
