/* eslint-disable react/button-has-type */

import React, { Component } from 'react';
import { connect } from 'react-redux';
import 'tachyons';
import DeleteTwoToneIcon from '@material-ui/icons/DeleteTwoTone';
import Dialog from '@material-ui/core/Dialog';
import DialogActions from '@material-ui/core/DialogActions';
import DialogContent from '@material-ui/core/DialogContent';
import AddAPhotoOutlinedIcon from '@material-ui/icons/AddAPhotoOutlined';
import DialogTitle from '@material-ui/core/DialogTitle';
import Slide from '@material-ui/core/Slide';
import { Draggable } from 'react-beautiful-dnd';

import './styles.css';
import Switch from '../../../common/Components/Switch';


const Transition = React.forwardRef((props, ref) => <Slide direction="up" ref={ref} {...props} />);

function importAll(r) {
  return r.keys().map(r);
}

const logos = importAll(require.context('../../../assets/Images/LinkLogos/', false, /\.(png|jpe?g|svg)$/));
class AdminPage extends Component {
  constructor(props) {
    super(props);
    this.state = {
      render: false,
      updated: false,
      loading: false,
      iconDialogOpen: false,
      error: '',
      isNormalUser: true,
      link: {},
    };
    this.handleIconDialogOpen = this.handleIconDialogOpen.bind(this);
    this.onCustomIconSelect = this.onCustomIconSelect.bind(this);
  }

  componentWillMount() {
    let temp;

    if (this.props.user) {
      temp = this.props.user.user.user_type === 'normal';
    }
    this.setState({
      updated: false, link: this.props.link, showDelete: false, iconSrc: this.props.link.icon, isNormalUser: temp,

    });
  }

  componentWillReceiveProps(np) {
    if (np.link !== this.state.link) {
      this.setState({ render: !this.state.render, updated: false, showDelete: false });
    }
  }

  handleTextChange(key, value) {
    this.setState({ error: '' });
    this.setState({ link: { ...this.state.link, [key]: value }, updated: true, showDelete: false });
  }

  createLink(e, link) {
    // this.setState({ error: '' });
    e.preventDefault();
    // console.log(link);
    if (link.url && link.title) {
      this.setState({ showDelete: false });

      this.props.createLink(link);
    } else {
      this.setState({ error: 'Title or url cannot be empty!!!' });
    }
  }

  deleteLink(link) {
    this.props.deleteLink(link);
  }

  togelSwitch(val) {
    this.setState({ link: { ...this.state.link, visible: (val.target.checked) }, updated: true, showDelete: false });
  }

  handleIconDialogOpen() {
    this.setState({ iconDialogOpen: !this.state.iconDialogOpen });
  }

  selectIcon(icon) {
    fetch(icon)
      .then((response) => response.blob())
      .then((blob) => {
        blob.name = 'icon';
        blob.lastModified = new Date();
        blob.lastModifiedDate = new Date();
        blob.webkitRelativePath = icon;
        this.setState({
          link: { ...this.state.link, icon: blob }, iconSrc: icon, iconDialogOpen: false, updated: false,
        });
        this.uploadLinkIcon(this.state.link, blob);
      });
  }

  uploadLinkIcon(link, icon) {
    this.props.uploadLinkIcon(link, icon);
  }

  async onCustomIconSelect(e) {
    if (e.target.files && e.target.files.length > 0) {
      const file = e.target.files[0];
      const imageDataUrl = await readFile(file);

      this.handleIconDialogOpen();
      this.setState({
        iconSrc: imageDataUrl,
        link: { ...this.state.link, icon: file },
        updated: false,
      });
      this.uploadLinkIcon(this.state.link, file);
    }
  }

  render() {
    const { title, url, visible } = this.state.link;
    const { iconSrc, isNormalUser } = this.state;

    return (
      <Draggable
        draggableId={`${this.props.link.index + 100}`}
        index={this.props.link.index}

      >
        {
        (provided) => (
          <div
            className=" link-div"
            ref={provided.innerRef}
            {...provided.draggableProps}
            {...provided.dragHandleProps}
          >
            <form onSubmit={(e) => this.createLink(e, this.state.link)}>
              <div className="flex justify-between">
                <div className="flex flex-column w-80">

                  <input
                    className="b"
                    value={title}
                    placeholder="Enter Link Title"
                    onChange={(e) => this.handleTextChange('title', e.target.value)}
                  />
                  <input
                    value={url}
                    type="url"
                    placeholder="https://example.com"
                    onChange={(e) => this.handleTextChange('url', e.target.value)}
                  />
                </div>
                <Switch togelSwitch={this.togelSwitch.bind(this)} val={visible} />
              </div>
              {
                !isNormalUser
                  ? (
                    <div className="visible-link-icon">
                      <p>Link Icon :</p>
                      {
                  iconSrc ? (
                    <div className="add-link-icon-btn" onClick={this.handleIconDialogOpen}>
                      <img src={iconSrc} alt="" />
                    </div>
                  ) : (
                    <div className="add-link-icon-btn" onClick={this.handleIconDialogOpen}>
                      <AddAPhotoOutlinedIcon style={{ color: 'white' }} />
                    </div>
                  )
                }

                    </div>
                  )
                  : null
              }

              <p className="err-txt">{`${this.state.error}`}</p>
              <div className="flex justify-end">

                <div className="flex items-center justify-center">
                  {
                this.state.updated ? (
                  <button className="color-btn" type="submit">
                    Save
                  </button>
                ) : null
              }
                  <DeleteTwoToneIcon className="pointer " onClick={() => this.setState({ showDelete: true })} />
                </div>

              </div>
              {
            this.state.showDelete ? (
              <div className=" ">
                <p className="b"> Are you sure to delete link? </p>
                <div className="flex items-center mt2 justify-between">
                  <button className="color-btn w-50 tc" onClick={() => this.setState({ showDelete: false })} type="button">Cancel</button>
                  <button className="color-btn w-50 tc " onClick={() => this.deleteLink(this.state.link)} type="button">Delete</button>
                </div>

              </div>
            ) : null
          }
              <Dialog
                open={this.state.iconDialogOpen}
                TransitionComponent={Transition}
                keepMounted
                onClose={this.handleIconDialogOpen}
                aria-labelledby="alert-dialog-slide-title"
                aria-describedby="alert-dialog-slide-description"
              >
                <DialogTitle id="alert-dialog-slide-title">Choose Icon for your link.</DialogTitle>
                <DialogContent style={{ display: 'flex', flexWrap: 'wrap' }}>
                  {
                logos.map((l, i) => (
                  <button className="link-logo-img" type="button" onClick={() => this.selectIcon(l)}>
                    <img src={l} key={i} />
                  </button>
                ))
              }
                </DialogContent>
                <DialogActions>
                  <div className="center" className="select-img-icon color-btn">
                    <input
                      type="file"
                      id="avatar"
                      name="avatar"
                      accept="image/png, image/jpeg"

                      onChange={this.onCustomIconSelect}
                    />
                      Choose custom icons
                  </div>


                  <button onClick={() => this.selectIcon(null)} color="primary" className="btn ba ">
                    Remove Icon
                  </button>
                  <button onClick={this.handleIconDialogOpen} color="primary" className="btn ba ">
                Cancel
                  </button>

                </DialogActions>
              </Dialog>
            </form>

          </div>
        )
            }
      </Draggable>

    );
  }
}

function readFile(file) {
  return new Promise((resolve) => {
    const reader = new FileReader();
    reader.addEventListener('load', () => resolve(reader.result), false);
    reader.readAsDataURL(file);
  });
}

const mapStateToProps = (state) => ({
  user: state.AuthReducer.user,
});

const mapDispatchToProps = (dispatch) => ({
});

export default connect(
  mapStateToProps,
  mapDispatchToProps,
)(AdminPage);
