
import React, { Component } from 'react';
import 'tachyons';
import { connect } from 'react-redux';
import './styles.css';

import { APP_NAME } from '../../constants/otherConstants';
import instalink_logo from '../../assets/Images/instalink_logo.png';
import { GET_HEADER_INFO } from '../../constants/actionTypes';
import agent from '../../agent';

class Logo extends Component {
  constructor(props) {
    super(props);
    this.state = {
      style: {},
      user: {},
      headerInfo: {
        header_icon: instalink_logo,
        header_text: APP_NAME,
      },
    };
  }

  componentWillMount() {
    if (this.props.logostyles) {
      this.setState({ style: this.props.logostyles });
    }
    if (this.props.parent !== 'login' && this.props.parent !== 'signup') {
      if (this.props.headerLogo) {
        this.setState({ headerInfo: this.props.headerInfo });
      } else if (this.props.user) {
        console.log(this.props);
        const { username, user_type } = this.props.user.user;
        const temp = user_type === 'normal';
        if (!temp) {
          const data = { username };
          this.props.getHeaderInfo(data);
        }
        this.setState({ user: this.props.user.user });
      }
    }
  }

  componentWillReceiveProps(np) {
    if (this.props.parent !== 'login' && this.props.parent !== 'signup') {
      if (np.changedHeaderInfo && np.changedHeaderInfo !== this.state.headerInfo) {
        this.setState({ headerInfo: np.changedHeaderInfo });
      } else if (np.headerInfo && np.headerInfo !== this.state.headerInfo) {
        this.setState({ headerInfo: np.headerInfo });
      }
      if (np.user && np.user.user !== this.state.user) {
        
        const { username, user_type } = np.user.user;
        const temp = user_type === 'normal';
        if (!temp) {
          const data = { username };
          console.log(data, 'user data firing fucntion ');
          this.props.getHeaderInfo(data);
        }
        this.setState({ user: np.user.user });
      }
    }
  }


  render() {
    const { header_text, header_icon } = this.state.headerInfo;
    const { style } = this.state;
    return (
      <div className="img-div pointer " style={style}>
        <img src={header_icon} alt="" />
        <p>{`${header_text}`}</p>
      </div>
    );
  }
}
const mapStateToProps = (state) => ({
  headerInfo: state.MyAccountReducers.headerInfo,
  user: state.AuthReducer.user,
});

const mapDispatchToProps = (dispatch) => ({
  getHeaderInfo: (user) => dispatch({ type: GET_HEADER_INFO, payload: agent.MyAccount.getHeaderInfo(user) }),
});
export default connect(mapStateToProps, mapDispatchToProps)(Logo);
